import React from "react";
import { CiViewList } from "react-icons/ci";
import { MdDeleteOutline, MdEditNote } from "react-icons/md";
import styled from "styled-components";
import useAxios from "../../api/Axios";
import loadingImg from "../images/1_CsJ05WEGfunYMLGfsT2sXA.gif";
import { useDispatch } from "react-redux";
import { setViewData } from "../../reduxjs/ViewInfoSlice";
import { useMarksEntry } from "../../Context/MarksEntry";
import {
  TiArrowSortedDown,
  TiArrowSortedUp,
  TiArrowUnsorted,
} from "react-icons/ti";

const TableForMarks = ({ classInfo }) => {
  const axios = useAxios();
  const marks = useMarksEntry();
  const [student, setStudent] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  React.useEffect(() => {
    getAllStudent();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [axios, marks.classInfo, marks.search, marks.sortTable.order]);

  window.addEventListener("load", () => {
    getAllStudent();
  });
  const getAllStudent = async () => {
    const url = "/student/all?id=" + classInfo.classId;
    await axios.get(url).then((res) => {
      setStudent(res.data);
      setLoading(false);
    });
  };

  return (
    <div>
      {loading ? (
        <Loading>
          <Image src={loadingImg} alt="Loading..." />
        </Loading>
      ) : (
        <React.Fragment>
          <Table width="100%">
            <THead>
              <TableHead classInfo />
            </THead>
            <TBody>
              {student.length > 0 &&
                student
                  .sort((a, b) => {
                    const order = marks.sortTable.order;
                    const field = marks.sortTable.field;
                    if (order === "") {
                      return a;
                    } else {
                      if (order === "asc") {
                        return a[field] > b[field] ? 1 : -1;
                      } else {
                        return a[field] > b[field] ? -1 : 1;
                      }
                    }
                  })
                  .filter((data) => {
                    if (marks.search !== "") {
                      if (
                        Object.values(data)
                          .toString()
                          .toLocaleLowerCase()
                          .includes(marks.search.toString().toLowerCase())
                      ) {
                        return data;
                      }
                      return null;
                    } else {
                      return data;
                    }
                  })
                  .map((data) => {
                    return (
                      <TableBodyRows
                        key={data.id}
                        data={data}
                        classInfo={classInfo}
                      />
                    );
                  })}
            </TBody>
          </Table>
        </React.Fragment>
      )}
    </div>
  );
};

export default TableForMarks;

const Table = styled.table`
  width: 100%;
  font-size: 0.9rem;
  font-weight: 500;
  padding: 1px;
  border-radius: 5px;
  box-sizing: border-box;
  border-color: gray;
`;

const THead = styled.thead``;

const TBody = styled.tbody``;

const TRow = styled.tr``;

const TData = styled.td`
  border: 1px solid rgba(3, 3, 3, 0.3);
  padding: 0.3rem 0.5rem;
  &:nth-child(n + 4) {
    text-align: center;
  }
`;

const TH = styled.th`
  border: 1px solid rgba(3, 3, 3, 0.4);
  padding: 0.3rem 0.5rem;
  font-size: 0.8rem;
  text-align: center;
  font-weight: 600;
  min-width: 75px;
`;

const IconPanel = styled.div`
  display: flex;
  justify-items: stretch;
  align-items: stretch;
`;

const Icon = styled.div`
  width: 25px;
  height: auto;
  font-size: 1.1rem;
  margin: 0 0.2rem;
  cursor: pointer;
  border-radius: 5px;

  &:hover {
    background: rgba(230, 230, 230, 0.8);
  }
`;

const Loading = styled.div`
  width: 100%;
  height: 55vh;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Image = styled.img`
  width: 200px;
  height: auto;
  border-radius: 5px;
`;

function TableBodyRows({ data }) {
  const dispatch = useDispatch();
  const marks = useMarksEntry();
  return (
    <TRow key={data.id}>
      <TData>{data.stuId}</TData>
      <TData style={{ whiteSpace: "nowrap" }}>{data.stuName}</TData>
      <TData style={{ textAlign: "center" }}>{data.attendance}</TData>
      {marks.classInfo.courseType === "Lab" ? (
        <TData>{data.labPerform}</TData>
      ) : null}
      <TData>{data.assign1}</TData>
      <TData>{data.assign2}</TData>
      <TData>{data.assign3}</TData>
      <TData>{data.average}</TData>
      <TData>{data.test1}</TData>
      <TData>{data.test2}</TData>
      <TData>{data.best}</TData>
      <TData>{data.total}</TData>
      <TData>
        <IconPanel>
          <Icon
            onClick={() => {
              dispatch(setViewData({ id: data.id }));
            }}
          >
            <CiViewList />
          </Icon>
          <Icon>
            <MdEditNote />
          </Icon>
          <Icon>
            <MdDeleteOutline />
          </Icon>
        </IconPanel>
      </TData>
    </TRow>
  );
}

const TableHead = ({ classInfo }) => {
  const marks = useMarksEntry();

  const handleSortField = (field) => {
    const order = marks.sortTable.order;
    if (order === "desc") {
      marks.handleSort({ order: "asc", field });
    } else {
      marks.handleSort({ order: "desc", field });
    }
  };
  return (
    <React.Fragment>
      <TRow>
        <TH
          rowSpan="2"
          style={{ cursor: "pointer", width: "100px" }}
          onClick={() => handleSortField("stuId")}
        >
          Student ID <SortIcon field={"stuId"} />
        </TH>
        <TH rowSpan="2">Student Name</TH>
        <TH
          rowSpan="2"
          style={{ cursor: "pointer", width: "100px" }}
          onClick={() => handleSortField("attendance")}
        >
          Class Attendance ({classInfo.courseType === "Lab" ? 10 : 5}){" "}
          <SortIcon field={"attendance"} />
        </TH>
        {/**
         * Lab Performance
         */}
        {classInfo.courseType === "Lab" ? (
          <TH
            rowSpan="2"
            style={{ cursor: "pointer" }}
            onClick={() => handleSortField("labPerform")}
          >
            Lab Performance(10) <SortIcon field={"labPerform"} />
          </TH>
        ) : null}
        <TH colSpan="4">Assignment/Case Study (10)</TH>
        <TH colSpan="3">
          Quizzes/Class Test ({classInfo.courseType === "Lab" ? 10 : 15})
        </TH>
        {/**
         * Total CA Marks
         */}
        <TH
          rowSpan="2"
          style={{ cursor: "pointer", whiteSpace: "nowrap" }}
          onClick={() => handleSortField("total")}
        >
          Total ({classInfo.courseType === "Lab" ? 40 : 30}){" "}
          <SortIcon field={"total"} />
        </TH>
        <TH rowSpan="2">Action</TH>
      </TRow>
      <TRow>
        <TH>Assign-1</TH>
        <TH>Assign-2</TH>
        <TH>Assign-3</TH>
        {/**
         * Average Assignment Marks
         */}
        <TH
          style={{ cursor: "pointer", whiteSpace: "nowrap" }}
          onClick={() => handleSortField("average")}
        >
          Average <SortIcon field={"average"} />
        </TH>
        <TH>Test-1</TH>
        <TH>Test-2</TH>
        {/**
         * Best of Quizz/Test Marks
         */}
        <TH
          style={{ cursor: "pointer", whiteSpace: "nowrap" }}
          onClick={() => handleSortField("best")}
        >
          Best <SortIcon field={"best"} />
        </TH>
      </TRow>
    </React.Fragment>
  );
};

const SortIcon = ({ field }) => {
  const marks = useMarksEntry();
  if (marks.sortTable.order !== "" && marks.sortTable.field === field) {
    if (marks.sortTable.order === "asc") {
      return <TiArrowSortedDown />;
    } else return <TiArrowSortedUp />;
  } else {
    return <TiArrowUnsorted />;
  }
};
